.container {
  text-align: center;
  position: absolute;
  left: 0;
  right: 0;
  top: 50%;
  transform: translateY(-50%);
}

.container strong {
  font-size: 20px;
  line-height: 26px;
}

.container p {
  font-size: 16px;
  line-height: 22px;
  color: #8c8c8c;
  margin: 0;
}

.container a {
  text-decoration: none;
}

.schedule-dates ion-label {
  width: 80px;
  font-family: 'Roboto' !important;
  align-content: center;
  text-align: center;
  font-weight: 600;
  letter-spacing: 2px;
  color: gray !important;
}